import {computed, ref} from "vue";
import {useRouter} from "vue-router";

export const useAuthorization = () =>{
  const login = ref({
    value: '',
    error: ''
  })

  const password = ref({
    value: '',
    error: ''
  })

  const router = useRouter();

  const isSubmit = computed(()=>{
    if(!login.value.value || !password.value.value){
      return false;
    }

    return true
  })
  const submit = () =>{
    router.push({name: 'home'})
  }

  return { login, password, isSubmit, submit }
}