import {ref} from "vue";

export const useTabs = (tabs: Array<String>, initialActive: String) =>{
  const tabsValue = ref(tabs)
  const tabActive = ref(initialActive)

  const setActive = (newTab: string) => {
    tabActive.value = newTab;
  }

  const isActive = (tab: String) =>{
    return tab === tabActive.value
  }

  return {tabsValue, isActive, setActive}
}