<template>
  <teleport to="body">
    <div v-if="shown" class="toast__PWA">
      <p class="toast__PWA-title">
        Установить приложение на Ваше устройство?
      </p>

      <div class="toast__PWA-wrapper-btn">
        <button class="toast__PWA-button" @click="installPWA">
          Установить!
        </button>

        <button class="toast__PWA-button" @click="dismissPrompt">
          Нет, спасибо
        </button>
      </div>
    </div>
  </teleport>
</template>

<script>
export default {
  data: () => ({
    shown: false,
  }),

  beforeMount() {
    window.addEventListener('beforeinstallprompt', (e) => {
      e.preventDefault()
      this.installEvent = e
      this.shown = true
    })
  },

  methods: {
    dismissPrompt() {
      this.shown = false
    },

    installPWA() {
      this.installEvent.prompt()
      this.installEvent.userChoice.then((choice) => {
        this.dismissPrompt() // Hide the prompt once the user's clicked
        if (choice.outcome === 'accepted') {
          // Do something additional if the user chose to install
        } else {
          // Do something additional if the user declined
        }
      })
    },
  }
}
</script>

<style scoped>
.toast__PWA {
  position: fixed;
  bottom: 50px;
  left: 50%;
  transform:translateX(-50%);
  display: flex;
  flex-direction: column;
  align-items: center;
  background-color: var(--primary-black);
  color: white;
  padding: 8px 12px;
  border-radius: 12px;
  font-size: 15px;
  text-align: center;
  animation: view .3s 1 ease;
}
.toast__PWA-title {
  font-family: 'MTS Compact', sans-serif;
  margin-bottom: 5px;
}
.toast__PWA-wrapper-btn {
  display: flex;
  align-items: center;
  width: 100%;
}
.toast__PWA-button {
  background-color: transparent;
  border: none;
  padding: 8px 12px;
  color: white;
  flex: 1 1 auto;
  cursor: pointer;
  font-family: 'MTS Compact', sans-serif;
  transition: background-color .2s ease;
  border-radius: 6px;
  font-size: 17px;
  font-weight: 500;
}

.toast__PWA-button:hover {
  background-color: #2e2e2e;
}

@keyframes view {
  0%{
    transform: translate(-50%,300px);
  }
  100% {
    transform:translateX(-50%);
  }
}

@media (max-width: 576px) {
  .toast__PWA-button {
    font-size: 15px;
    font-weight: 400;
  }

  .toast__PWA {
    width: 300px;
    bottom: 30px;
  }
}
</style>