import { reactive} from "vue";

export const useToggle = (stateProps: boolean) =>{
  const toggle = reactive({
    state: stateProps,

    init: function (state: boolean) {
      this.state = state || false;
    },

    is: function (state: boolean) {
      return state === this.state;
    },

    on: function () {
      this.state = true;
    },

    off: function () {
      this.state = false;
    },

    switch: function () {
      this.state = !this.state;
    },
  });

  return { toggle };
}