import { createRouter, createWebHistory } from 'vue-router'
import HomeView from '../views/HomeView.vue'
import AuthView from '../views/AuthView.vue'

export const routes = [
  {
    path: '/auth',
    name: 'auth',
    component: AuthView,
    meta: {
      layout: 'Auth',
    }
  },
  {
    path: '/',
    name: 'home',
    component: HomeView,
    meta: {
      layout: 'Main',
    },
    children:[
      {
        path:'/create',
        name: 'create',
        component: HomeView,
      },
      {
        path:'/parking',
        name: 'parking',
        component: HomeView,
      },
      {
        path:'/settings',
        name: 'settings',
        component: HomeView,
      }
    ]
  },
]

const router = createRouter({
  history: createWebHistory(process.env.BASE_URL),
  routes
})

export default router
